@font-face {
  font-family: "PTsans";
  src: url("../fonts/PTsans.woff2") format("woff2"), url("../fonts/PTsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PTsans";
  src: url("../fonts/PTsansbold.woff2") format("woff2"), url("../fonts/PTsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  width: 1200px;
  margin: 0 auto;
  font-family: "PTsans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 26px;
  color: #333;
  background-color: #f1f1f1; }

.wrapper {
  width: 1056px;
  margin: 0 auto; }

.overflow {
  overflow: hidden; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.btn {
  font-family: "PTsans", "Arial", sans-serif;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  transition: all 0.3s;
  border: none; }
  .btn--blue {
    background-color: #81b3d1; }
    .btn--blue:hover, .btn--blue:focus {
      background-color: #669ec0; }
  .btn--brown {
    background-color: #756357; }
    .btn--brown:hover, .btn--brown:focus {
      background-color: #604e43; }
  .btn:active {
    filter: brightness(0.7); }

.content {
  margin: 0;
  padding: 0; }
  .content h3 {
    font-size: 21px;
    line-height: 21px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase; }
  .content p {
    text-transform: uppercase; }

.content__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.content__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  order: 3;
  width: 400px;
  text-align: center; }
  .content__item:first-child {
    flex-direction: row;
    order: 0;
    width: 100%;
    color: #fff;
    background-color: #81b3d1; }
  .content__item:nth-child(2) {
    flex-direction: row-reverse;
    order: 2;
    width: 100%;
    color: #fff;
    background-color: #81b3d1; }
  .content__item:nth-child(6), .content__item:nth-child(7), .content__item:last-child {
    order: 1;
    background-color: #fff; }

.content__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 400px;
  text-align: center; }

.intro {
  padding-top: 807px;
  background-color: #fff;
  background-image: url("../img/intro.png");
  background-repeat: no-repeat;
  background-position: center top; }
  .intro h2 {
    width: 576px;
    margin: 0 auto;
    font-size: 21px;
    line-height: 26px;
    font-weight: 700;
    color: #000;
    text-transform: uppercase;
    text-align: center; }
  .intro p {
    text-align: center;
    text-transform: uppercase; }

.main-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff; }

.main-footer__hashtag {
  font-size: 21px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000; }

.main-footer__copyright {
  display: flex;
  align-items: center;
  width: 240px;
  color: #000;
  text-transform: uppercase; }

.main-header {
  background-color: #fff; }

.main-nav {
  position: relative; }

.main-nav__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%); }

.main-nav__menu {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-between; }

.main-nav__link {
  display: block;
  padding: 20px 0;
  font-weight: 700;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s; }
  .main-nav__link:hover, .main-nav__link:focus {
    color: #81b3d1; }
  .main-nav__link:active {
    color: #000;
    opacity: 0.3; }

.map {
  height: 471px;
  background-image: url("../img/map.png");
  background-repeat: no-repeat;
  background-position: center top; }

.search {
  position: relative;
  background-color: #fff;
  text-align: center; }
  .search h2 {
    font-size: 30px;
    line-height: 36px;
    font-weight: 700;
    text-transform: uppercase;
    color: #000; }
  .search p {
    text-transform: uppercase; }

.search__btn {
  padding: 20px 140px; }

.search__form {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 570px;
  background-color: #fff; }

.search-form {
  display: flex;
  flex-wrap: wrap;
  padding: 35px 55px; }

.search-form label,
.search-form ::placeholder,
.search-form input {
  font-weight: 700;
  text-transform: uppercase;
  color: #000; }

.search-form input {
  margin: 0;
  width: 345px;
  padding: 10px 40px 10px 12px;
  background-color: #f1f1f1;
  border: none; }

.search-form__row {
  display: flex;
  justify-content: space-between;
  width: 100%; }
  .search-form__row:nth-last-of-type(2), .search-form__row:last-of-type {
    width: 50%; }
  .search-form__row:nth-last-of-type(2) input,
  .search-form__row:last-of-type input {
    width: 38px; }

.search-form__col button {
  margin: 0;
  padding: 0;
  width: 38px;
  height: 38px;
  border: none; }

.search-form__btn {
  width: 100%; }

.social {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 150px; }

.social__link {
  display: block;
  width: 44px;
  height: 44px;
  background-color: #81b3d1;
  background-repeat: no-repeat;
  background-position: center; }
  .social__link--tw {
    background-image: url("../img/sprite/icon-twitter.svg"); }
  .social__link--fb {
    background-image: url("../img/sprite/icon-facebook.svg"); }
  .social__link--yt {
    background-image: url("../img/sprite/icon-youtube.svg"); }
